import React, { Component } from 'react';
import './commonHeader.css';
import './ranking.css';
import logo from './logo.svg';
import './App.css';
import { withRouter } from 'react-router';
import {BrowserRouter, Switch, Route, Link } from 'react-router-dom';
import { Button, Container, Form, FormGroup, Input, Navbar, Nav } from 'reactstrap';
import Modal from "react-modal";
import Ranking from './ranking';


class CommonHeader extends Component {

	constructor(props) {
    super(props);
    this.state = {
      isOpen: false,
      ranking: null
    };
  }

	// ランキング情報を取得する
	handleToRanking(e) {
		var request = require('superagent');
	    e.preventDefault();
	    const url = '/ranking';
	    request
	    .get(url)
	    .then(res => {
	    	this.setState({isOpen: true, ranking: res.body});
	    })
	    .catch(err => {
	    	this.props.history.push({
				  pathname: '/error'
			  })
			});

	  }
	// ランキングポップアップを閉じる
	handleToClose(e) {
		this.setState({isOpen: false});
	}

	render() {

		const customStyle= {
      overlay: {
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: 'rgba(255, 255, 255, 0.75)'
      },
      content: {
        position: 'absolute',
        top: '40px',
        left: '40px',
        right: '40px',
        bottom: '40px',
        border: '1px solid #ccc',
        background: 'green',
        overflow: 'auto',
        WebkitOverflowScrolling: 'touch',
        borderRadius: '4px',
        outline: 'none',
        padding: '20px'
      }
		}

    return (
      <div>
        <Navbar color="light" light expand="md">
            <Nav className="ml-auto" navbar>
            	<Button type="link" onClick={this.handleToRanking.bind(this)}>ランキング表示をする</Button>
            </Nav>
	      </Navbar>
	      <Modal
	        style={customStyle}
	        isOpen={this.state.isOpen}
	      >
	        <h2 id="user_register_title">茶 圓 ポ ー カ ー ランキング</h2>
	        <Ranking
	          ranking={this.state.ranking}
	          handleToClose={this.handleToClose}
	        />
	        <Button id="closeBunnton"  onClick={this.handleToClose.bind(this)}>閉じる</Button>
	      </Modal>
	    </div>
    );
  }

}

export default withRouter(CommonHeader);