import React, { Component } from 'react';
import Modal from "react-modal";
import { Button } from 'reactstrap';
import './loginBonusPopup.css';

class LoginServicePopUp extends Component {

	constructor(props) {
    super(props);
    this.state = {
      isOpen: this.props.isOpen,
    };
  }

	handleToClose(e) {
		this.setState({isOpen: false});
	}

	render() {
		if(!this.props.username) {
			return null;
		}
		const customStyle= {
      overlay: {
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: 'rgba(255, 255, 255, 0.75)'
      },
      content: {
        position: 'absolute',
        top: '40px',
        left: '40px',
        right: '40px',
        bottom: '40px',
        border: '1px solid #ccc',
        background: 'green',
        overflow: 'auto',
        WebkitOverflowScrolling: 'touch',
        borderRadius: '4px',
        outline: 'none',
        padding: '20px'
      }
	}
    return (
      <div>
        <Modal
          className="Modal"
          isOpen={this.state.isOpen}
        >
         <h2 id="message">{this.props.username}さん本日初ログインです。100円贈呈します。</h2>
        <Button id="closeBunnton" onClick={this.handleToClose.bind(this)}>閉じる</Button>
      </Modal>

	    </div>
    );
  }

}

export default LoginServicePopUp;
